<template>
  <div class="main">
    <Spinner></Spinner>

    <div class="main__block">
      <div class="main__block-title">ВИДЕО</div>
      <div class="main__line"></div>
      <div class="main__video-block">
        <div class="main__video-block-item">
          <iframe width="100%" height="315" src="https://www.youtube.com/embed/862DMHi9sNA" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          <div class="main__video-block-item-title">Фильм о компании МЕГАМИКС</div>
        </div>
        <div class="main__video-space"></div>
        <div class="main__video-block-item">
          <iframe width="100%" height="315" src="https://www.youtube.com/embed/3KWwI4oIsT0" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          <div class="main__video-block-item-title">Фильм о правильном приготовлении смеси с удобрением "МЕГАМИКС - Семена" </div>
        </div>
      </div>
    </div>

    <div class="main__block">
      <div class="main__block-title">КУЛЬТУРЫ</div>
      <div class="main__block-subtitle">Применение удобрений "МЕГАМИКС" на разных культурах</div>
      <div class="main__line"></div>
      <div class="main__item-block">
        <div class="veget link" v-for="element of veget" :key="element.id" @click="chooseVeg(element.id)" >
          <div class="veget-img"><img :src="element.img" alt="" class="veget-img"></div>
          <div class="veget-name">{{element.name}}</div> 
        </div>
      </div>
    </div>

    <div class="main__block">
      <div class="main__block-title">ПРОДУКТЫ</div>

      <div class="main__block-subtitle">Содержание элементов питания и использование для различных растений</div>
      <div class="main__line"></div>
      <div class="main__item-block">
        <div class="goods link" v-for="element of goods" :key="element.id">
          <div class="goods-img"><img :src="element.img" alt="" class="goods-img" @click="chooseProd(element.id)"></div>
          <div class="goods-name">МЕГАМИКС-{{element.name}}</div> 
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Spinner from '@/components/Spinner/Spinner'


export default {
  name: 'Main',
  components: {
    Spinner
  },
  data () {
    return {
      veget: [
        {
          id: 0,
          name: 'Зерновые',
          img: 'static/cultures/Rye.png'
        },
        {
          id: 1,
          name: 'Подсолнечник',
          img: 'static/cultures/sunflower.png'
        },
        {
          id: 2,
          name: 'Кукуруза',
          img: 'static/cultures/corn.png'
        },
        {
          id: 3,
          name: 'Сахарная свекла',
          img: 'static/cultures/shugars.png'
        },
        {
          id: 4,
          name: 'Картофель',
          img: 'static/cultures/pottatoo.png'
        },
        {
          id: 5,
          name: 'Зернобобовые',
          img: 'static/cultures/bobs.png'
        },
        {
          id: 6,
          name: 'Рапс',
          img: 'static/cultures/raps.png'
        },
        {
          id: 7,
          name: 'Лён',
          img: 'static/cultures/len.png'
        },
      ],
      goods: [
        {
          id: 0,
          name: 'СЕМЕНА',
          img: 'static/tara/semena.png'
        },
        {
          id: 1,
          name: 'ПРОФИ',
          img: 'static/tara/semena.png'
        },
        {
          id: 2,
          name: 'БОР',
          img: 'static/tara/bor.png'
        },
        {
          id: 3,
          name: 'МАГНИЙ_ЦИНК',
          img: 'static/tara/Mg-Zn_2.png'
        },
        {
          id: 4,
          name: 'СЕРА',
          img: 'static/tara/Sera_2.png'
        },
        {
          id: 5,
          name: 'АЗОТ',
          img: 'static/tara/azot.jpeg'
        },
        {
          id: 6,
          name: 'ФОСФОР',
          img: 'static/tara/Fosfor.png'
        },
        {
          id: 7,
          name: 'КАЛИЙ',
          img: 'static/tara/kaliy1.png'
        },
      ]
    }
  },
  methods: {
    chooseProd(val){
        this.$router.push('/product/'+val)
    },
    chooseVeg(val){
      this.$router.push('/culture/'+val)
    }
  }
}
</script>


<style>
  .main{
    position: relative;
    width: 100%;
    color: #4e504f;
  }
  .main__block{
    margin-bottom: 100px;
  }
  
  .main__video-block{
    width: 90%;
    display: inline-flex;
    text-align: justify;
  }
  
  .main__video-block-item{
    width: 45%;
    display: inline-block;
  }
  .main__video-space{
    width: 9%;
    display: inline-block;
  }
  .main__video-block-item-title{
    text-align: left;
    margin-top: 20px;
  }
  li{
    margin: 0;
  }
  .main__block-title{
    font-size: 30px;
    color: #4D4F51;
    margin:0 0 20px 0;
    letter-spacing: 1.9px;
  }
  .main__block-subtitle{
    font-size: 18px;
    color: #4D4F51;
  }
  .main__line{
    position: relative;
    width: 120px;
    border-bottom: 1px solid #4e504f;
    left: 50%;
    margin: 30px 0 10px -60px; 
    margin-bottom: 90px;
  }
  
</style>


