                  text: 'В России содержание в почве микроэлементов на довольно низком уровне. Вместе с этим в нашей стране высокая микробиологическая активность почвы. Отличие комплекса МЕГАМИКС от западных технологий состоит в смещении приоритета внесения питания с вегетации на семена. Большое количество действующего вещества в препарате МЕГАМИКС – СЕМЕНА снижает конкуренцию за питание с микрофлорой и обеспечивает полноценное питание растений в ранней фазе их развития. Применение технологии МЕГАМИКС позволяет снизить дозировки и стоимость обработок по вегетации, обеспечить оперативность внесения и повысить технологичность. Полный комплекс МЕГАМИКС, обработка семян плюс вегетация, как правило, более выгодное и профессиональное решение по сравнению с западными технологиями.'
              }    
          ]
        },
        {
          id: 6,
          title: 'МЕГАМИКС для предпосевной обработки в условиях сухого сева',
          img: 'static/articles/article0.png',
          text: [
              {
                class: 'par',
                text: 'При обработке МЕГАМИКСОМ не происходит преждевременной стимуляции семян к прорастанию. Поскольку микроэлементы являются катализатором биологических реакций, то они действуют на зародыш, только когда семя само запустит этот механизм, т.е. начнёт вырабатывать ферменты и гормоны. Влага - один из основных факторов прорастания семян. Дыхание зерна злаков, а значит и биологическая активность, начинает возрастать при влажности более 15-16%, но микроэлементы первоначально проникают в семя пассивно с током воды, соответственно эффект от них зависит от степени увлажнения. Оптимальная влажность для прорастания порядка 50-60% и от веса зерновки'
              },
              {
                  class: 'par',
                  text: 'Одна из опасностей сухого сева в недоступности основных удобрений из почвы. Когда проросток развивается только благодаря запасам питания в семени. В таких условиях особое значение приобретает NPK в составе МЕГАМИКС. Их небольшого количества проросткам хватает на первые 2-3 недели развития и в то же время микроэлементы активизируют процессы поступления питательных веществ из семени, что важно при недостаточном увлажнении эндосперма.'
              },
              {
                  class: 'par',
                  text: 'Такой комплексный подход МЕГАМИКС к питанию и стимулированию начального роста зародыша позволяет увеличить энергию прорастания, полевую всхожесть и ускорить начальные фазы развития, которые являются критическим периодом по устойчивости к осенней засухе. Поскольку чем больше биомасса, тем больше проросток может запасти в ней влаги. Этому же способствует и усиление фотосинтеза, сахара которого связывают воду внутри растения, снижая испарение, идут на рост растения и служат источником энергии и воды при дыхании растения.'
              },
              {
                  class: 'par',
                  text: 'Отдельно стоит отметить интенсивное развитие корневой системы при обработке МЕГАМИКС. С одной стороны корни быстрее проникают в более глубокие и влажные горизонты почвы. С другой, развитая корневая мочка охватывает больший объём почвы и способна впитать больше влаги в случае кратковременных дождей или выпадения внутрипочвенной росы.'
              },
              {
                  class: 'par',
                  text: 'Часть элементов питания поглощается микрофлорой около семени. Что локально увеличивает микробное число и биологическую активность почвы. А вместе с этим количество закреплённой в микроорганизмах влаги и количество выделяемых ферментов, что облегчает питание культуры.'
              }       
          ]
        },
        {
          id: 7,
          title: 'Работайте с удобрениями правильно...',
          img: 'static/articles/article0.png',
          text: [
              {
                class: 'par',
                text: 'По мере развития агрохимии внесение микроэлементов в почву применяется все реже. Современное применение микроудобрений идет путем их внесения на семена и с помощью некорневых подкормок. Эти методы позволяют повысить эффективность их усвоения культурой. '
              },
              {
                  class: 'par',
                  text: 'Популярность некорневых подкормок и микроэлементных препаратов пришли в Россию из Европы, вместе с рекомендациями, адаптированными для европейских почв, климата и рыночных условий. В Европе на многих почвах наблюдается высокий уровень содержания микроэлементов, часто приближенный к ПДК. Чтобы не превышать этот показатель, на западе для обработки семян применяют минимальные дозировки микроудобрений. Внесение микроэлементов по вегетации обеспечивает минимальное попадание их в почву. '
              },
              {
                  class: 'par',
                  text: 'В России содержание в почве микроэлементов на довольно низком уровне. Вместе с этим в нашей стране высокая микробиологическая активность почвы. Отличие комплекса МЕГАМИКС от западных технологий состоит в смещении приоритета внесения питания с вегетации на семена. Большое количество действующего вещества в препарате МЕГАМИКС – СЕМЕНА снижает конкуренцию за питание с микрофлорой и обеспечивает полноценное питание растений в ранней фазе их развития. Применение технологии МЕГАМИКС позволяет снизить дозировки и стоимость обработок по вегетации, обеспечить оперативность внесения и повысить технологичность. Полный комплекс МЕГАМИКС, обработка семян плюс вегетация, как правило, более выгодное и профессиональное решение по сравнению с западными технологиями.'
              }    
          ]
        },
        {
          id: 8,
          title: 'А от аммиачной селитры весной отказаться можно?',
          img: 'static/articles/article0.png',
          text: [
              {
                class: 'par',
                text: 'Интересный вопрос задал один из фермеров Ростовской области: а можно ли обойтись без аммиачной селитры на озимой пшенице весной? Вопрос неожиданный, но уточнив ситуацию всё стало на свои места. Оказывается, с прошлого года предшественники под озимую пшеницу чистый пар и горох, при этом признаков азотного голодания нет. Ну и как работать азотными удобрениями без ранневесеннего внесения аммиачной селитры?'
              },
              {
                  class: 'par',
                  text: 'Ответ на поставленный вопрос: да, отказаться внесения аммиачной селитры ранней весной можно, заменив этот агроприём на листовую подкормку при возобновлении вегетации и прикорневое внесение азота в кущение.'
              },
              {
                  class: 'par',
                  text: 'В чистом пару и после зернобобовых в почве как правило азота накапливается достаточно для начального развития культур и при этом нет большого количества биомассы, требующей весь этот азот для минерализации микрофлорой.'
              },
              {
                class: 'par',
                text: 'Листовая подкормка азотом в момент возобновления вегетации обеспечит вегетативную массу питанием быстрее чем через корни, позволив регенерировать повреждения, полученные за зиму и начать быстро развиваться. Невысокая температура воздуха и наличие влаги при этом позволяют дать в подкормку раствор мочевины около 10%. Но даже при таком значительном количестве азота, эта листовая подкормка будет работать всего 2-3 недели. А значит нужно стимулировать максимальное усвоение этого азота, например, комплексным микроэлементным удобрением МЕГАМИКС-Профи. В случае значительного повреждения культуры желательно дополнительно добавить в раствор фунгицид (например, Фитоспорин, обладающий стимулирующим эффектом) и удобрение МЕГАМИКС - Магний-Цинк для усиления фотосинтеза и углеводного обмена'
              },
              {
                  class: 'par',
                  text: 'Прикорневое внесение основной части азота, для длительного питания культуры, необходимо в ф. кущения с заделкой в почву ротационной бороной или врезанием сеялками. Так удобрения попадут во влажную почву или непосредственно к корням. А умеренные механические повреждения позволят продлить у пшеницы фазу весеннего кущения, даже при высоких температурах воздуха и почвы. Кроме ставших привычными сухих азотных удобрений можно использовать и КАС, азот из которого будет дольше доступен растениям. Тут важно не опоздать, т.к. при механических повреждениях в ф. трубкования можно получить негативный эффект.'
              },
              {
                  class: 'par',
                  text: 'Не лишним будет и продублировать листовую подкормку при работе с гербицидом в ф. кущения. Поскольку азот в почве будет, а наличие влаги обуславливает его доступность культуре, то подкормку рационально сделать микроэлементным удобрением МЕГАМИКС-Профи, усиливающим все биопроцессы, в т.ч. корневое питание'
              } ,
              {
                  class: 'par',
                  text: 'Касательно предшественников, дозировки надо скорректировать. Зернобобовые получают основную часть азота из воздуха, благодаря азотфиксации клубеньковых бактерий, но большую его часть выносят с урожаем. К тому же оставшийся в пожнивно-корневых остатках азот будет высвобождаться постепенно, по мере их разложения. И значительная его часть уйдёт собственно на разложение этой биомассы. Азот от минерализации гумуса так же не накопится на поле с зерно-бобовой культурой, т.к. поглощался растениями в ходе вегетации. По этому дозировку азота вносимого в почву после зерно-бобовых целесообразно увеличить на 10-20% по сравнению с полями по чистому пару.'
              },
              {
                class: 'par',
                text: 'Разумеется, следует оставить и остальные листовые подкормки в ф. флаг. лист и при работе на качество в ф. молочно-восковой спелости. Поскольку общее внесение азота сокращено, особое внимание следует уделить своевременности его внесения'
              },
              {
                  class: 'par',
                  text: 'Погода и прочие факторы могут внести свои корректировки в данные рекомендации и надо оперативно на них отреагировать. Например, выпадение осадков в кущение-трубкование больше чем обычно и продление периода корневого питания могут спровоцировать излишнее формирование вегетативной массы. Тогда сделать акцент на последних листовых подкормках, активизирующих созревание и отток питательных веществ в зерно. А часть азота в этих подкормках заменить на серу (МЕГАМИКС-Сера)'
              },
              {
                  class: 'par',
                  text: 'Так же нередки ситуации с длительной засухой начиная с момента схода снега. Тут решающими могут оказаться дополнительные подкормки, снижающие абортирование боковых стеблей в трубкование и череззёрицу в колошение. При этом можно сократить внесение азота в почву, сделав акцент на листовые подкормки, эффективные в засуху.'
              },
              {
                  class: 'par',
                  text: 'Подведём итоги: ранневесеннее внесение аммиачной селитры можно заменить на более поздние прикорневые и листовые азотные подкормки; при сокращении общего азота возрастает важность своевременности его внесения и применение веществ, стимулирующих его усвоение и эффективность; по различным предшественникам надо делать поправку на потери азота для минерализации растительных остатков; технология должна по возможности реагировать на изменяющиеся условия, как погодные, так и технологические, и финансовые.'
              }              
          ]
        },
      ]
    }
  },
  methods: {
    chooseProd(val){
          this.$router.push('/article/'+val)
    },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
    .step{
        margin-left: 5%;
        font-weight: 100;
    }
    .articles{
      position: relative;
      width: 25%;
      display: inline-block;
      vertical-align: top;
      margin-bottom: 60px;
    }
    .articles-title{
      color:#707070;
      font-size: 24px;
      padding-right: 15%;
      text-align: justify;
      margin-top: 20px;
      
    }
    .articles-img{
      position: relative;
      width: 100%;
      height: 100%;
      padding-right: 8%;
    }
    
</style>



